import { defineStore } from "pinia";

export const useCartStore = defineStore("cart", {
  state: () => ({
    cartItems: JSON.parse(localStorage.getItem("cartItems")) || [], //    LocalStorage
    shippingCost: Number(localStorage.getItem("shippingCost")) || 55, //     localStorage   

  }),
  actions: {

    addToCart(product, quantity) {
      const existingItem = this.cartItems.find((item) => item.id === product.id);
      if (existingItem) {
        //      
        existingItem.quantity += quantity;
      } else {
        //     
        this.cartItems.push({ ...product, quantity });
      }
      this.saveCart();
    },

    removeFromCart(productId) {
        //        id
        this.cartItems = this.cartItems.filter(item => item.id !== productId);
        this.saveCart();
      },


    saveCart() {
      //    LocalStorage
      localStorage.setItem("cartItems", JSON.stringify(this.cartItems));
    },
    updateShippingCost(cost) {
      this.shippingCost = cost; //    
      localStorage.setItem("shippingCost", cost); //    localStorage
    },
    
    clearCart() {
      this.cartItems = [];
      this.saveCart();
    },
  },
});
